html, body {
  color-scheme: light dark;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-size: 16px;
}

body {
  overflow: auto;
}

body.modal--open {
  overflow: hidden;
}

main {
  position: relative;
}

h1 {
  font-size: 3rem;
  font-weight: 700;
  line-height: 1.2;
}

p {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
}

.hero__section {
  justify-content: center;
  align-items: flex-end;
  height: 240px;
  padding: 1.5rem;
  display: flex;
}

.hero-content__section {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  display: flex;
}

.cat {
  border: 1px solid #161616;
  border-radius: 100%;
  width: 192.1px;
  height: 170px;
  position: relative;
}

.ear {
  background: #161616;
  width: 25%;
  height: 60%;
  position: absolute;
  top: -30%;
}

.ear:before, .ear:after {
  content: "";
  background: #fff;
  border-radius: 50%;
  width: 5%;
  height: 10%;
  position: absolute;
  bottom: 30%;
}

.ear:after {
  transform-origin: 50% 100%;
}

.ear--left {
  border-radius: 70% 30% 0% 0% / 100% 100% 0% 0%;
  left: -3%;
  transform: rotate(-15deg);
}

.ear--left:before, .ear--left:after {
  right: 13%;
}

.ear--left:after {
  transform: rotate(-45deg);
}

.ear--right {
  border-radius: 30% 70% 0% 0% / 100% 100% 0% 0%;
  right: -3%;
  transform: rotate(15deg);
}

.ear--right:before, .ear--right:after {
  left: 13%;
}

.ear--right:after {
  transform: rotate(45deg);
}

.face {
  background: #161616;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  position: absolute;
}

.eye {
  background: #fff;
  border: 1px solid #161616;
  border-radius: 50%;
  width: 25%;
  height: 30%;
  position: absolute;
  top: 25%;
}

.eye--left {
  left: 15%;
}

.eye--left:before {
  right: -5%;
}

.eye--right {
  right: 15%;
}

.eye--right:before {
  left: -5%;
}

.eye-pupil {
  background: #161616;
  border-radius: 50%;
  width: 25%;
  height: 35%;
  position: absolute;
  top: 25%;
}

.eye--left .eye-pupil {
  right: 30%;
}

.eye--right .eye-pupil {
  left: 30%;
}

.muzzle {
  background: #855868;
  border-radius: 50% / 30% 30% 70% 70%;
  width: 10%;
  height: 6%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.mouth {
  background: #fff;
  border-radius: 0% 0% 50% 50% / 0 0 70% 70%;
  width: 20%;
  height: 10%;
  position: absolute;
  top: 70%;
  left: 50%;
  transform: translateX(-50%);
}

.mouth:before {
  content: "";
  background: #161616;
  border-radius: 50% / 100% 100% 0 0;
  width: 100%;
  height: 20%;
  position: absolute;
  bottom: -30%;
  left: 50%;
  transform: translateX(-50%);
}

.whisker {
  width: 30%;
  height: 2px;
  position: absolute;
  top: 65%;
}

.whisker:before, .whisker:after {
  content: "";
  background: #565553;
  width: 100%;
  height: 100%;
  position: absolute;
}

.whisker--left {
  left: -10%;
}

.whisker--left.whisker--one {
  top: 65%;
}

.whisker--left.whisker--two {
  top: 70%;
}

.whisker--left.whisker--three {
  top: 75%;
}

.whisker--right {
  right: -10%;
}

.whisker--right.whisker--one {
  top: 65%;
}

.whisker--right.whisker--two {
  top: 70%;
}

.whisker--right.whisker--three {
  top: 75%;
}

.hero-content__section h1 {
  text-align: center;
  margin: 0;
  line-height: 1.2;
}

.hero-content__section .hero-content__feelings-container {
  flex-direction: column;
