  display: flex;
}

.hero-content__section .hero-content__feelings-container > p {
  margin: 0;
}

.hero-content__section .hero-content__feelings-container > p:last-child {
  text-align: center;
  margin-top: .5rem;
}

.hero-content__section .hero-content__button {
  color: #fff;
  cursor: pointer;
  background-color: #161616;
  border: none;
  border-radius: .25rem;
  margin: 2rem 0;
  padding: 1rem 2rem;
  font-size: 1rem;
  transition: background-color .3s;
}

.hero-content__section .hero-content__button:active, .hero-content__section .hero-content__button:focus, .hero-content__section .hero-content__button:hover {
  background-color: #2d2d2d;
}

.modal {
  backdrop-filter: blur(5px);
  background-color: #000000b3;
  border: none;
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal.modal--hidden {
  display: none;
}

.data-dialog {
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.data-dialog__figure {
  margin: 0;
}

.data-dialog__close-button {
  color: #fff;
  cursor: pointer;
  background-color: #161616;
  border: none;
  padding: .25rem .5rem;
  font-size: 1.25rem;
  position: absolute;
  top: 0;
  right: 0;
}

.data-dialog__close-button:active, .data-dialog__close-button:focus, .data-dialog__close-button:hover {
  background-color: #2d2d2d;
}

.data-dialog__image {
  width: 100%;
  height: 100%;
  display: block;
}

.data-dialog__loader-container {
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
  display: flex;
}

.data-dialog__loader-container.not-loading {
  display: none;
}

.loader {
  box-sizing: border-box;
  border: 10px solid #ffffff26;
  border-color: #ffffff26 #ffffff40 #ffffff59 #ffffff80;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: 1s linear infinite rotation;
  display: inline-block;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
